import React from 'react';
import {genPromise} from '../plugins';
import 'styled-components';
import {News} from 'styled-icons/boxicons-regular/News';
import {FilePdf} from 'styled-icons/icomoon/FilePdf';
import {Home6} from 'styled-icons/remix-line/Home6';
import ResultView from './result.jsx';

class PaperItem extends React.Component{
    render(){
        return(
            <div className='card'>
                {this.props.conf&&<div className='card-header'>{this.props.conf}</div>}
                <div className='card-body'>
                    <h6 className='card-title'>{this.props.title}</h6>
                   
                    <p className='card-text'>{this.props.author}</p>
                    {this.props.link&&<a className='card-link btn btn-self' href={this.props.link}>
                        <FilePdf className='mr-1' size='15'/>
                        Paper</a>}
                </div>
            </div>
        )
    }
}
export default class Home extends React.Component{
    constructor(props){
        super(props);
        this.state={
            paper_list:[],
            error:false,
            error_msg:'error'
        }
    }
    
    componentDidMount(){
        genPromise('GET','http://39.100.113.162/info/paper/getPaperByYear.php',{
            limit:6
        }).then(
            (data)=>{
                if(data.code===0){
                    this.setState({
                        paper_list:data.data,
                        error:false
                    })
                }else{
                    this.setState({
                        error:true
                    })
                }
            },(error)=>{
                console.log(error);
               
                this.setState({
                    error:true,
                    error_msg:error.message
                })
            }
        )
    }

    render(){
        const new_paper=this.state.paper_list.map((el)=>
            <PaperItem  key={el.id}
                        link={el.pdf_link}
                        author={el.author}
                        conf={el.conf}
                        title={el.title} />
        );
        return(
        <div className='home row border-left border-right border-bottom'>
            
            <div className='paper-container col-sm-8'>
                <h4>
                    <News className='align-bottom' size='30'/>
                     最新论文
                </h4>
                {
                    this.state.error?
                    <ResultView text={this.state.error_msg} />:
                    (<div className='card-group'>
                        {new_paper}
                    </div>)
                }
               
            </div>
            <div className="info-container col-sm-4">
                <h4>
                    <Home6 className='align-bottom' size='30 '/>
                     关于我们</h4>
                <p>本项目组共 有一名指导老师，肖晶副教授，共有4名博士，21名硕士。
                    项目组共担任过3项国家级重点项目，4项国家自然科学基金项目，1项国际项目，
                    2项省级重大项目，其他重大项目2项。此外，项目组还在AItrans和ICME Grand Chanllenge等
                    重大比赛中取得了优异成绩。
                </p>
            </div>
        </div>
        )
    }
}