import React from 'react';
import Header from './header';
import {genPromise} from '../plugins';
import {Link} from 'react-router-dom';
import ResultView from './result';

class UserItem extends React.Component{
    render(){
        return(
            <div className='card user-item'>
                <img className='card-img-top rounded-circle' alt="用户头像" 
                src={this.props.img?this.props.img:"http://localhost/test/img/default.jpg"}/>
                <div className='card-body'>
                    <Link className='card-text' to={"/people/"+this.props["data-id"]}>{this.props.name}</Link>
                </div>
            </div>
        )
    }
}

export class UserSingle extends React.Component{
    constructor(props){
        super(props);
        this.state={
            name:'',
            enroll_year:'',
            email:'',
            research_orientation:'',
            degree:'',
            graduate_orientation:'',
            link:'',
            imgUrl:'',
            papers:[],
            patents:[],
            load:false,
            error:false,
            error_msg:'error'
        };
    }

    componentDidMount(){
        genPromise('GET','http://39.100.113.162/info/user/getById.php',{
            id:this.props.match.params.id
        }).then((data)=>{
            if(data.code===0){
                this.setState(Object.assign(data.data[0],{
                    load:true
                }));
            }else{
                this.setState({
                    error:true,
                    error_msg:'error'
                })
            }
            
        },(error)=>{
            console.log(error);
            this.setState({
                error:true,
                error_msg:error.message
            })
        })
    }

    render(){
        /*const teacher_dl=[
            {name:'name',text:'姓名'},
            {name:'email',text:'email'},
            {name:'research_orientation',text:'研究方向'}
        ],
            graduate_dl=[
                {name:'name',text:'姓名'},
                {name:'email',text:'email'},
                {name:'enroll_year',text:'入学年份'},
                {name:'degree',text:'学位'},
                {name:'research_orientation',text:'研究方向'}
            ],
            postgraduate_dl=[
                {name:'name',text:'姓名'},
                {name:'email',text:'email'},
                {name:'enroll_year',text:'入学年份'},
                {name:'degree',text:'学位'},
                {name:'research_orientation',text:'研究方向'},
                {name:'graduate_orientation',text:'毕业去向'}
            ];
*/
        const graduate_dt_dd=[
            <dt>毕业去向</dt>,
            <dd>{this.state.graduate_orientation}</dd>
        ];
        var papers_dd=this.state.papers.map((el)=>
            (
                <li key={el.id}>
                    {el.degree&&(`[${el.degree}]`)}{el.author}.{el.title}.{el.conf},{el.year}
                </li>
            )
        ),
            patents_dd=this.state.patents.map((el)=>
            (
                <li key={el.id}>
                    {el.title} {el.authorized_id} 发明人:{el.author}
                </li>
            )
        );
        
        
                if(this.state.error){
                    return(
                        <div><Header />
                        <div className='user-container container'>
                            
                            <ResultView text={this.state.error_msg} />
                        </div></div>
                    )
                }
                else if(!this.state.load){
                    return(
                        <div><Header />
                    <div className='user-container container user-signle'>
                        
                        <div className='d-flex content-loader flex-wrap justify-content-around'>
                            <div className='d-flex flex-column flex-wrap'>
                                <div className='conent-loader-circle rounded-circle bg-grey animated-bg m-3'
                                    style={{width:'10rem',height:'10rem'}}></div>
                                <div>
                                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                        style={{width:'8rem',height:'1rem'}}></div>
                                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                        style={{width:'8rem',height:'1rem'}}></div>
                                </div>
                            </div>
                            <div>
                                <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                    style={{width:'15rem',height:'1rem'}}></div>
                                <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                    style={{width:'12rem',height:'1rem'}} ></div>
                            </div>
                        </div>
                   </div></div>)
                }
                else{
                    return(
                        <div><Header />
            <div className='user-container container user-single'>
                
                <div className='user-item card'>
                    <Link to={this.state.link?this.state.link:"#"}>
                        <img className='card-img-top rounded-circle' 
                        src={this.state.imgUrl?this.state.imgUrl:"http://localhost/test/img/default.jpg"} alt='用户头像'/>

                    </Link>
                    <div className='card-body'>
                        <p className='card-title font-weight-bold'>{this.state.name}</p>
                        <p>{this.state.email}</p>
                        {
                            this.state.type!==2&&
                                <p>{this.state.enroll_year+"级"+this.state.degree}</p>
                            
                        }
                    </div>
                </div>
                <dl>
                    <dt>研究方向</dt>
                    <dd>{this.state.research_orientation}</dd>
                    {
                        this.state.type===1?graduate_dt_dd:''
                    }
                    {this.state.papers.length>0&&<dt>发表论文</dt>}
                    {this.state.papers.length>0&&<dd><ul>{papers_dd}</ul></dd>}
                    {this.state.patents.length>0&&<dt>发表专利</dt>}
                    {this.state.patents.length>0&&<dd><ul>{patents_dd}</ul></dd>}

                </dl>
            </div></div>)
            }
                
       
    }
}

export default class User extends React.Component{
    constructor(props){
        super(props);
        this.state={
            teacher_list:[],
            graduate_list:[],
            postgraduate_list:[],
            load_teacher:false,
            load_graduate:false,
            load_postgraduate:false,
            error:false,
            error_msg:'error'
        };
    }

    componentDidMount(){

        genPromise('GET','http://39.100.113.162/info/user/teacher.php').then((data)=>{
            if(data.code===0){
                this.setState({
                    teacher_list:data.data,
                    load_teacher:true
                })
            }else{
                this.setState({
                    error:true
                })
            }

        },(error)=>{
            console.log(error);
            this.setState({
                error:true,
                error_msg:error.message
            })
        })


        genPromise('GET','http://39.100.113.162/info/user/graduate.php').then((data)=>{
            if(data.code===0){
                this.setState({
                    graduate_list:data.data,
                    load_graduate:true
                })
            }else{
                this.setState({
                    error:true
                })
            }

        },(error)=>{
            console.log(error);
            this.setState({
                error:true,
                error_msg:error.message
            })
        })

        genPromise('GET','http://39.100.113.162/info/user/postgraduate.php').then((data)=>{
            if(data.code===0){
                this.setState({
                    postgraduate_list:data.data,
                    load_postgraduate:true
                })
            }else{
                this.setState({
                    error:true
                })
            }

        },(error)=>{
            console.log(error);
            this.setState({
                error:true,
                error_msg:error.message
            })
        })
    }

    render(){
        const teacher_list= this.state.teacher_list.map((el)=>
                <UserItem key={el.id} img={el.imgUrl} 
                name={el.name} data-id={el.id} />
            )
,
              graduate_list=this.state.graduate_list.map((el)=>
                <UserItem key={el.id} img={el.imgUrl} name={el.name} data-id={el.id}/>
            ),
              postgraduate_list= this.state.postgraduate_list.map((el)=>
                <UserItem key={el.id} img={el.imgUrl} name={el.name} data-id={el.id}/>
            );

        var content_list=[];
        for(let i=0;i<8;i++){
            content_list.push(
                <div key={i} className='p-2' style={{width:'12rem'}}>
                    <div className='conent-loader-circle rounded-circle bg-grey animated-bg m-3'
                        style={{width:'8rem',height:'8rem'}}></div>
                    
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'50%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'40%',height:'.8rem'}}></div>
                   
                </div>
                
            )
        }

       if(this.state.error){
           return(
               <div><Header />
               <div className='container user-container'>
                   <ResultView text={this.state.error_msg} />
               </div></div>
           )
       }
       else if(this.state.load_teacher&&this.state.load_graduate&&this.state.load_postgraduate)
       //if(false)
            return(
                <div>
                    <Header/>
                <div className='container user-container'>
                    
                    <h4 className='title border-bottom'>成员</h4>
                    <h4 className='subtitle'>teacher</h4>
                    <div className='card-deck'>
                        {teacher_list}
                    </div>
                    <h4 className='subtitle'>graduate</h4>
                    <div className='card-deck'>
                        {graduate_list}
                    </div>
                    <h4 className='subtitle'>postgraduate</h4>
                    <div className='card-deck'>
                        {postgraduate_list}
                    </div>
                </div></div>
                );
        else
            return(
                /*<div className='d-flex justify-content-center spinner-box'>
                   <div className="spinner-border text-light" role="status">
                        <span className="sr-only">Loading...</span>
                    </div> 
                </div> */
                <div><Header />
                <div className='container user-container'>
                       
                        <div className='w-100 p-2'>
                            <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                style={{width:'85%',height:'.8rem'}}></div>
                            <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                                style={{width:'60%',height:'.8rem'}}></div>
                            
                        </div>
                        <div className='d-flex flex-wrap'>
                            {content_list}
                         </div>
                    
                </div></div>);
               
        
    }
}